import { useState } from "preact/hooks";
import { Range } from "../../../shared/ui";
import styles from "./Calculator.module.css";

const Calculator = ({ a, b, currency = "", pricePerHour = 70 }) => {
  const [first, setFirst] = useState(a);
  const [second, setSecond] = useState(b);

  const onChangeFirst = (item) => {
    const a = first.map((it) => {
      if (it.title === item.title) {
        return { ...it, checked: true };
      }

      return { ...it, checked: false };
    });

    setFirst(a);
  };

  const onChangeSecond = (item) => {
    const a = second.map((it) => {
      if (it.title === item.title) {
        return { ...it, checked: true };
      }

      return { ...it, checked: false };
    });

    setSecond(a);
  };

  const f = first.find((item) => item.checked).value;
  const s = second.find((item) => item.checked).value;

  const price = f * s * pricePerHour;

  return (
    <div>
      <Range className={styles.firstRange} grad items={first} onChange={onChangeFirst} />
      <Range className={styles.secondRange} circle items={second} onChange={onChangeSecond} />

      <div className={styles.price}>
         : {price} {currency}
      </div>
    </div>
  );
};

export { Calculator };
